export const Button = (props: { label: string; children: React.ReactNode }) => {
  return <button aria-role={props.label}>{props.children}</button>;
};

export const List = (props: { str: string }) => {
  return <div>{props.str}</div>;
};

export const Test = <T extends string | string[]>(props: { str: T }) => {
  return <span></span>;
};
